import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import forumList from '@/views/pages/forum/list'
import zhuanjiaList from '@/views/pages/zhuanjia/list'
import zhuanjiaDetail from '@/views/pages/zhuanjia/formModel'
import zhuanjiaAdd from '@/views/pages/zhuanjia/formAdd'
import yaocairenzhengxinxiList from '@/views/pages/yaocairenzhengxinxi/list'
import yaocairenzhengxinxiDetail from '@/views/pages/yaocairenzhengxinxi/formModel'
import yaocairenzhengxinxiAdd from '@/views/pages/yaocairenzhengxinxi/formAdd'
import yaocaifenleiList from '@/views/pages/yaocaifenlei/list'
import yaocaifenleiDetail from '@/views/pages/yaocaifenlei/formModel'
import yaocaifenleiAdd from '@/views/pages/yaocaifenlei/formAdd'
import yaocaiziyuanList from '@/views/pages/yaocaiziyuan/list'
import yaocaiziyuanDetail from '@/views/pages/yaocaiziyuan/formModel'
import yaocaiziyuanAdd from '@/views/pages/yaocaiziyuan/formAdd'
import yaocaigongxiaoList from '@/views/pages/yaocaigongxiao/list'
import yaocaigongxiaoDetail from '@/views/pages/yaocaigongxiao/formModel'
import yaocaigongxiaoAdd from '@/views/pages/yaocaigongxiao/formAdd'
import yaocaishuxingList from '@/views/pages/yaocaishuxing/list'
import yaocaishuxingDetail from '@/views/pages/yaocaishuxing/formModel'
import yaocaishuxingAdd from '@/views/pages/yaocaishuxing/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'zhuanjiaList',
			component: zhuanjiaList
		}, {
			path: 'zhuanjiaDetail',
			component: zhuanjiaDetail
		}, {
			path: 'zhuanjiaAdd',
			component: zhuanjiaAdd
		}
		, {
			path: 'yaocairenzhengxinxiList',
			component: yaocairenzhengxinxiList
		}, {
			path: 'yaocairenzhengxinxiDetail',
			component: yaocairenzhengxinxiDetail
		}, {
			path: 'yaocairenzhengxinxiAdd',
			component: yaocairenzhengxinxiAdd
		}
		, {
			path: 'yaocaifenleiList',
			component: yaocaifenleiList
		}, {
			path: 'yaocaifenleiDetail',
			component: yaocaifenleiDetail
		}, {
			path: 'yaocaifenleiAdd',
			component: yaocaifenleiAdd
		}
		, {
			path: 'yaocaiziyuanList',
			component: yaocaiziyuanList
		}, {
			path: 'yaocaiziyuanDetail',
			component: yaocaiziyuanDetail
		}, {
			path: 'yaocaiziyuanAdd',
			component: yaocaiziyuanAdd
		}
		, {
			path: 'yaocaigongxiaoList',
			component: yaocaigongxiaoList
		}, {
			path: 'yaocaigongxiaoDetail',
			component: yaocaigongxiaoDetail
		}, {
			path: 'yaocaigongxiaoAdd',
			component: yaocaigongxiaoAdd
		}
		, {
			path: 'yaocaishuxingList',
			component: yaocaishuxingList
		}, {
			path: 'yaocaishuxingDetail',
			component: yaocaishuxingDetail
		}, {
			path: 'yaocaishuxingAdd',
			component: yaocaishuxingAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
